@use "../abstracts/variables" as *;

button {
  cursor: pointer;
}

#createIssue {
  position: absolute;
  bottom: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--buttonColor500);
  width: 4rem;
  height: 4rem;
  border-radius: 3rem;
  z-index: 1;
}

#addStatusUpdateButton {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--buttonColor500);
  width: 3rem;
  height: 3rem;
  border-radius: 3rem;
}

#createIssueAssignedToButton {
  width: fit-content;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  // background-color: var(--secundaryBackgroundColor600);
  // width: 3rem;
  // height: 3rem;
  // border-radius: 2rem;
}

#createIssuePrioritySelectButton {
  width: fit-content;
}

#createIssueSubmitButton,
#updateIssueButton {
  // display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  background-color: var(--buttonColor500);
  color: var(--secundaryTextColor);
  padding: 0.4rem 0;
  border-radius: 0.2rem;
  width: 11rem;
  align-self: center;
}

.filterOptionBadge {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: var(--secundaryBackgroundColor600);
  color: var(--secundaryTextColor);
  padding: 0.6rem;
  border-radius: 0.2rem;
  svg {
    margin-left: 0.7rem;
  }
}
